import "./styles.css";
import React, { useState } from "react";

export default function App() {
  const [Count, setCount] = useState(10);

  function inc() {
    setCount(function (aCount) {
      return (aCount += 1);
    });
  }
  function dec() {
    setCount(function (aCount) {
      return (aCount -= 1);
    });
  }

  return (
    <div className="App">
      <h1>React Counter</h1>
      <h1>{Count}</h1>
      <button onClick={inc}>+</button>
      <button onClick={dec}>-</button>
      <button onClick={mul}>*</button>
    </div>
  );
}
